import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
/*  */
import { Picker, List,  Toast, WhiteSpace , WingBlank, InputItem} from 'antd-mobile';
/* form */
import { createForm } from 'rc-form';
/*  */
/*  */
import Title from '../../module/navTitle/navTitle';
/*  */
import Button from '../../../scss/button/button';
// start
import './retrievePassword.scss'
// end
var _that=null;
var codeTime=null;
class retrievePassword extends Component{
    constructor(){
        super();
    }
    componentWillMount(){
        _that=this;//this
        //
        this.state={
            phoneError:false,
            phoneValue:'',
            codeError:false,
            codeValue:'',
           // countTime:'60',//code
            codeTetx:"",
            isCode:true,
            firstPassError:false,
            firstPassValue:'',
            lastPassError:false,
            lastPassValue:'',
            backStageError:false,
            backStageValue:''

        }
    }
    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        //clearInterval(codeTime)
        componentStore.clear(this);
        //  end
    };
    retreat(){
        //alert('');
        //
        Utils.switchRoute('/login');
        clearInterval(codeTime);
    };
    //
    phoneBlur(){
        //console.log(_that)
        let phone=this.value;
        let reg = /^1[3|4|5|7|8][0-9]{9}$/; //  13 14 15 17 18
        let IsPhone=reg.test(phone);
        if(!phone){
            componentStore.update(_that,{
                phoneError:true
            })
            console.log('1')
            Toast.info('', 1);
        }else{
            if(IsPhone){
                //console.log('',phone.length)
                componentStore.update(_that,{
                    phoneValue:phone //
                })

            }else{
                componentStore.update(_that,{
                    phoneError:true
                })
                Toast.info('', 1);
            }

        }
    };
    //
    phoneFocus(){
        componentStore.update(_that,{
            phoneError:false
        })
    };
    //
    //
    backStageBlur(){
        if(!_that.state.backStageValue){
            componentStore.update(_that,{
                backStageError:true
            })
            Toast.info("",1)
        }
    }
    //
    backStageFocus(){
        componentStore.update(_that,{
            backStageError:false
        })
    }
    //backstageCode 
    backstageCode(){
        alert('');
    }
    //code
    codeBlur(){
        let code=this.value;
        let reg =/^[0-9]*$/ ;
        let codeV=reg.test(code);
        if(!code){
            componentStore.update(_that,{
                codeError:true
            })
            Toast.info('', 1);
        }else{
            if(codeV){
                componentStore.update(_that,{
                    codeValue:code //
                })
                // ajax

                //  
                //Toast.info('', 2);
            }
        }
        //console.log('',code)
    };
    codeFocus(){
        componentStore.update(_that,{
            codeError:false
        })
    }
    //code

    countTime(){
        componentStore.update(_that,{
            isCode:false
        })
        var count = 60;
         codeTime = setInterval(function(){
            count--;
            //console.log("",count)
            //console.log(_that)
            /*componentStore.update(_that,{
                codeText:count+""
            })*/
            _that.setState({
                codeTetx:count+""
            })
            if(count<=0){
                /*componentStore.update(_that,{
                    codeText:"",
                    isCode:true
                })*/
                _that.setState({
                    codeTetx:"",
                    isCode:true
                })
                clearInterval(codeTime)//
            }
        },1000)
    }
    //code
    codeBtn(){
        let phoneVal = _that.state.phoneValue;
        let reg = /^1[3|4|5|7|8][0-9]{9}$/; //  13 14 15 17 18
        let codeV=reg.test(phoneVal);
        if(phoneVal){
                //alert('OK')
            if(codeV){
                //alert('')
                if(_that.state.isCode){//code
                    _that.countTime();//
                    Toast.info('',1)
                }else{
                    //console.log('1');
                    Toast.info('1', 2);
                }
            }else {
                componentStore.update(_that,{
                    phoneError:true
                })
                Toast.info('',1)

            }
        }else{
            Toast.info('',1)
        }
        

    }
    //
    firstPassBlur(){
        //alert('1')
        let regPassword = /(?!^[0-9]+$)(?!^[a-z]+$)(?!^[^A-z0-9]+$)^.{6,16}$/;//
        let passVal=this.value;
        let pass = regPassword.test(passVal);
        if(pass){
            componentStore.update(_that,{
                firstPassValue:passVal //
            })
        }else{
            componentStore.update(_that, {
                firstPassError: true
            })
            Toast.info('', 1);
        }
    };
    firstPassFocus(){
        componentStore.update(_that,{
            firstPassError:false
        })
    }
    //
    lastPassBlur(){
        //alert('1')
        let passVal=this.value;
        let firstPass = _that.state.firstPassValue;
        let lastPass = this.value;
        if(passVal){
            if(firstPass == lastPass){
                componentStore.update(_that,{
                    lastPassValue:lastPass //
                })
            }else{
                componentStore.update(_that, {
                    lastPassError: true
                })
                Toast.info('', 1);
            }
        }else{
            componentStore.update(_that, {
                lastPassError: true
            })
            Toast.info('', 2);
        }
    };
    lastPassFocus(){
        componentStore.update(_that,{
            lastPassError:false
        })
    }
    //
    retrieveClick(){
        //
        //
        let phone=_that.state.phoneValue;
        let reg = /^1[3|4|5|7|8][0-9]{9}$/; //  13 14 15 17 18
        let IsPhone=reg.test(phone);
        if(phone != ''){
            if(!IsPhone ){
                Toast.info('', 1);
                componentStore.update(_that,{
                    phoneError:true
                })
                return false;
            }
        }else{
            Toast.info('', 1);
            componentStore.update(_that,{
                phoneError:true
            })
            return false;
        }
        //
        if(!_that.state.backStageValue){
            Toast.info('',1);
            componentStore.update(_that,{
                backStageError:true
            })
            return false;
        }
        //
        if(_that.state.codeValue == ''){
            componentStore.update(_that,{
                codeError:true
            })
            Toast.info('', 1);
            return false;
        }
        //
        let regPassword = /(?!^[0-9]+$)(?!^[a-z]+$)(?!^[^A-z0-9]+$)^.{6,16}$/;//
        let firstPass=_that.state.firstPassValue;
        let pass = regPassword.test(firstPass);
        if(firstPass !=''){
            if(!pass){
                componentStore.update(_that,{
                    firstPassError:true
                })
                Toast.info('', 1);
                return false;
            }
        }else{
            componentStore.update(_that,{
                firstPassError:true
            })
            Toast.info('', 1);
            return false;
        }
        //
        let lastPass = _that.state.lastPassValue;
        if(lastPass != ''){
            if(firstPass != lastPass){
                componentStore.update(_that,{
                    lastPassError:true
                })
                Toast.info('', 2);
                return false;
            }
        }else{
            componentStore.update(_that,{
                lastPassError:true
            })
            Toast.info('', 2);
            return false;
        }

        Toast.info('', 5);
        //
        console.log("",phone);
        console.log("",_that.state.codeValue);
        console.log("",firstPass);
        console.log("",lastPass);

        //ajax
    }
    render(){
        const { getFieldProps } = this.props.form;
        return (
             <div>
                 <Title $id="lys_form"
                        title=""
                        retreat={this.retreat}
                        grade="2"
                 >
                 </Title>
                <div className="retrieve">
                    <div className="retrieve_title"></div>
                    <div className="retrieve_list">
                        <ul>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/register_phone.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_C">
                                        <InputItem
                                            {...getFieldProps('phone')}
                                            type="number"
                                            placeholder=""
                                            onBlur={this.phoneBlur}
                                            error={this.state.phoneError}
                                            onFocus={this.phoneFocus}
                                            value={this.state.phoneValue}
                                            onChange={(value)=>{
                                                this.state.phoneValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>
                            </li>
                            <li style={{display:_that.state.backStage}}>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/register_code.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast _right_C">
                                    <div className="code_input">
                                        <InputItem
                                            {...getFieldProps('code')}
                                            type="text"
                                            placeholder=""
                                            onBlur={this.backStageBlur}
                                            error={this.state.backStageError}
                                            onFocus={this.backStageFocus}
                                            value={this.state.backStageValue}
                                            onChange={(value)=>{
                                                this.state.backStageValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                    <div className="_center code_btn">
                                        <button className="countBtn" onTouchStart={this.backstageCode}></button>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/register_code.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast _right_C">
                                    <div className="code_input">
                                        <InputItem
                                            {...getFieldProps('code')}
                                            type="number"
                                            placeholder=""
                                            onBlur={this.codeBlur}
                                            error={this.state.codeError}
                                            onFocus={this.codeFocus}
                                            value={this.state.codeValue}
                                            onChange={(value)=>{
                                                this.state.codeValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                    <div className="_center code_btn">

                                        <button className="countBtn" onTouchStart={this.codeBtn}>{this.state.codeTetx}</button>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/register_password.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_C">
                                        <InputItem
                                            {...getFieldProps('firstPassWord')}
                                            type="password"
                                            placeholder=""
                                            error={this.state.firstPassError}
                                            onBlur={this.firstPassBlur}
                                            onFocus={this.firstPassFocus}
                                            value={this.state.firstPassValue}
                                            onChange={(value)=>{
                                                this.state.firstPassValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>
                            </li>
                            <li>
                                <div className="_left">
                                    <div className="_img">
                                        <img src={require('../../../image/icon/register_password.png')} alt=""/>
                                    </div>
                                </div>
                                <div className="_right _rightLast">
                                    <div className="_right_B">
                                        <InputItem
                                            {...getFieldProps('lastPassWord')}
                                            type="password"
                                            placeholder=""
                                            error={this.state.lastPassError}
                                            onBlur={this.lastPassBlur}
                                            onFocus={this.lastPassFocus}
                                            value={this.state.lastPassValue}
                                            onChange={(value)=>{
                                                this.state.lastPassValue = value;
                                                componentStore.update(this,this.state)
                                            }}
                                        >
                                        </InputItem>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div className="not_btn">
                        <button className="not_password">6~16</button>
                    </div>
                    <div className="retrieve_btm">
                        <div className="retrieve_btn">
                            <Button content="" click={this.retrieveClick} $id="retrieve" width="90%" lineH={0.85+'rem'} radius="8px" height={0.85+'rem'}></Button>
                        </div>
                    </div>
                </div>

             </div>
        )
    }

}
//const BasicInputExampleWrapper = createForm()(Forms);
const Global_retrievePassword = createForm()(retrievePassword);
export default Global_retrievePassword;



